// Wallet-related type definitions
import { CHAIN } from '@tonconnect/protocol';
export function createWalletInitConfigMnemonic(params) {
    return {
        mnemonic: params.mnemonic,
        version: params.version ?? 'v5r1',
        mnemonicType: params.mnemonicType ?? 'ton',
        walletId: params.walletId,
        network: params.network ?? CHAIN.MAINNET,
    };
}
export function isWalletInitConfigMnemonic(config) {
    return 'mnemonic' in config;
}
export function createWalletInitConfigPrivateKey(params) {
    return {
        privateKey: params.privateKey,
        version: params.version ?? 'v5r1',
        walletId: params.walletId,
        network: params.network ?? CHAIN.MAINNET,
    };
}
export function isWalletInitConfigPrivateKey(config) {
    return 'privateKey' in config;
}
export function createWalletInitConfigSigner(params) {
    const publicKey = typeof params.publicKey === 'string'
        ? Uint8Array.from(Buffer.from(params.publicKey.replace('0x', ''), 'hex'))
        : params.publicKey;
    return {
        publicKey: publicKey,
        version: params.version ?? 'v5r1',
        walletId: params.walletId,
        network: params.network ?? CHAIN.MAINNET,
        sign: params.sign,
    };
}
export function isWalletInitConfigSigner(config) {
    return 'publicKey' in config && 'sign' in config;
}
